import React from "react";

import "./index.css";

import { ArrayInfoSource } from "../../components";

const InfoPage = () => {
  const source = ArrayInfoSource;

  const boxes = [];

  for (let i = 0; i < 6; i++) {
    boxes.push(
      <div className="element-box" key={i}>
        <div className="image-box">{source[i].image}</div>
        <div className="temp-block">
          <div className="title-box grey">{source[i].title}</div>
          <div className="description-box grey">{source[i].description}</div>
        </div>
      </div>
    );
  }

  return (
    <div className="main-container">
      <span className="main-text grey">Why treaders all around the world</span>
      <span className="main-text yellow">select 7option?</span>

      <div className="blocks-text">{boxes.map(elem => elem)}</div>
    </div>
  );
};

export default InfoPage;
