import React, { useState } from "react";

const ValueSelect = ({
  titleText = "Title",
  source,
  preText = "",
  postText = "",
  iconPlus,
  iconMinus
}) => {
  const [currentValueIndex, setCurValIn] = useState(0);

  const styles = {
    icons: {
      width: "100%",
      fill: '#828ea0'
    },
    titleT: {
      fontSize: '12pt',
    },
    mainStyle: {
      height: '9vh',
      display: 'grid',
      gridTemplateRows: '1fr 7fr',
      borderBottom: '3px solid #828ea0',
      lineHeight: 2
    },
    secondaryStyle: {
      display: 'grid',
      gridTemplateColumns: '1fr 3fr 1fr',
    },
    source: {
      fontSize: '20pt',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'flex-start'
    }
  }
  return (
    <div className="select" style = {styles.mainStyle}>
      <span style = {styles.titleT}>{titleText}</span>
      <div style = {styles.secondaryStyle}>
        <div
          onClick={() => {
            if (currentValueIndex === 0) setCurValIn(source.length - 1);
            else setCurValIn(currentValueIndex - 1);
          }}
          style = {styles.icons}
        >
          {iconMinus}
        </div>
        <div style = {styles.source}>{preText + " " + source[currentValueIndex] + " " + postText}</div>
        <div
          onClick={() => {
            if (currentValueIndex === source.length - 1) setCurValIn(0);
            else setCurValIn(currentValueIndex + 1);
          }}
          style = {styles.icons}
        >
          {iconPlus}
        </div>
      </div>
    </div>
  );
};
export default ValueSelect