import React from "react";
import Chart from "react-apexcharts";

const ChartField = ({series, refChart, size}) => {
  const options = {
    chart: {
      yaxis: {
        min: 0,
        max: 8,
      },
      type: 'area',
      id: "basic-bar",
      animations: {
        enabled: true,
        easing: "easein",
        speed: 10,
        animateGradually: {
          enabled: false,
          delay: 150
        },
        dynamicAnimation: {
          enabled: true,
          speed: 600
        }
      },
    },
    grid: {
      borderColor: "rgba(45, 53, 67, 0.5)",
      strokeDashArray: 0,
      xaxis: {
        lines: {
          show: true,
          width: 1
        }
      },
    },
    fill: {
      type: "gradient",
      gradient: {
        shadeIntensity: 1,
        opacityFrom: 0.3,
        opacityTo: 0,
        stops: [0, 100],
        colorStops: [
          {
            offset: 0.6,
            color: "#FFF700",
            opacity: 0.3
          },
          {
            offset: 100,
            color: "#FFF700",
            opacity: 0.1
          }
        ],
      }
    },

    colors: ["#FFF700"],
    dataLabels: {
      enabled: false
    }
  };

    return (
      <Chart
        series={series || []}
        ref={refChart}
        options={options}
        height={size.height}
        width={size.width}
        type="line"
      />
    );
}

export default ChartField;
