import React, { useState } from "react";
import { UserMenu } from "../../components";
import store from "../../store";

import Dashboard from "./Dashboard";
import UserInf from "./UserInf";
import Deposit from "./Deposit";
import Withdraw from "./Withdraw";
import Operations from "./Operations";
import Deals from './Deals'

const Insides = () => {
  const { user } = store;
  const [forRender, setForRender] = useState(5);
  const callbackMenu = i => setForRender(i);
  const pages = [
    <UserInf gotoDashboard={() => callbackMenu(1)} />,
    <Dashboard />,
    <Deposit gotoDashboard={() => callbackMenu(1)} />,
    <Withdraw gotoDashboard={() => callbackMenu(1)} />,
    <Operations gotoDashboard={() => callbackMenu(1)} />,
    <Deals  gotoDashboard={() => callbackMenu(1)} />
  ];

  return (
    <>
      <UserMenu user={user} callback={callbackMenu} />
      {pages[forRender]}
    </>
  );
};

export default Insides;
