import React, { useState } from "react";
import { HeaderMenuItem, Button, Icon } from "../../../components";
import s from "./s.module.css";
import "./style.css";
import store from "../../../store";
import { DatePicker } from "antd";
import history from "./data";

const Operations = ({ gotoDashboard }) => {
  const { user } = store;
  const month = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Sep",
    "Oct",
    "Nov",
    "Dec"
  ];
  const [currentOperationsFilter, setOperationsFilter] = useState("All");
  const createView = array =>
    array.map(el => (
      <>
        <div className={s.dateTime}>
          <span>
            {el.date.getDate() + ' ' + month[el.date.getMonth()]}
          </span>
          <span>{el.time}</span>
        </div>
        <div>
          <span className={el.type === "Invoice" ? s.invoice : s.withdrawal}>
            {el.type}
          </span>
          <span>request #{el.requestNum}</span>
        </div>
        <div>
          <div>
            <Icon name={`sponsor-${el.paymentSystem}`} />
          </div>
        </div>
        <div>
          <span>{el.wallet}</span>
        </div>
        <div>
          <span className={el.type === "Invoice" ? s.invoice : s.withdrawal}>
            {(el.type === "Invoice" ? "+" : "-") + el.amount}$
          </span>
        </div>
      </>
    ));
  const [his, setHis] = useState(createView(history));
  const filterValues = ["All", "Withdrawal", "Invoice"];
  const [isFilterOpen, setIsFilterOpen] = useState(false);
  const [dateForFilter, setDateForFilter] = useState({
    start: null,
    end: null
  });

  return (
    <div className={s.main}>
      <HeaderMenuItem text="Operations" gotoDashboard={gotoDashboard} />
      <div className={s.mainContainer}>
        <span className={s.balance}>Balance</span>
        <div className={s.profile}>
          <img src={user.image} alt="" />
          <div>
            <span>Total</span>
            <br />
            <span className="yellow">${user.currencyCount.live}</span>
          </div>
        </div>
        <div className={s.horizontalLine} />
        <div className={s.pickers}>
          <div className={s.picker}>
            <div>
              <span>Birthday</span>
              <DatePicker
                placeholder="From"
                className={s.datePicker}
                onChange={val =>
                  setDateForFilter({ start: val._d, end: dateForFilter.end })
                }
              />
              <div className={s.arrow} />
            </div>
          </div>
          <div className={s.picker}>
            <div>
              <span>Birthday</span>
              <DatePicker
                placeholder="To"
                className={s.datePicker}
                onChange={val =>
                  setDateForFilter({ start: dateForFilter.start, end: val })
                }
              />
              <div className={s.arrow} />
            </div>
          </div>
          <div className={s.picker}>
            <div>
              <span>Operations</span>
              <div>{currentOperationsFilter}</div>
              <div
                className={`${s.arrow} ${isFilterOpen ? s.active : ""}`}
                onClick={() => setIsFilterOpen(!isFilterOpen)}
              />
              <div
                className={`${s.ddContent} ${isFilterOpen ? s.open : s.close}`}
              >
                {filterValues.map((el, i) => (
                  <div
                    key={i}
                    onClick={() => {
                      setOperationsFilter(el);
                      setIsFilterOpen(false);
                    }}
                  >
                    {el}
                  </div>
                ))}
              </div>
            </div>
          </div>
          <Button
            onClick={() => {
              let arr;
              if (currentOperationsFilter === "All") arr = history;
              else
                arr = history.filter(el => el.type === currentOperationsFilter);
              if (dateForFilter.start && dateForFilter.end) {
                arr = arr.filter(
                  el =>
                    el.date > dateForFilter.start && el.date < dateForFilter.end
                );
              } else if (dateForFilter.start) {
                arr = arr.filter(el => el.date > dateForFilter.start)
              } else if (dateForFilter.end) {
                arr = arr.filter(el => el.date < dateForFilter.end)
              }
              setHis(createView(arr));
            }}
            className={s.btnConfirm}
            text="Confirm"
          />
        </div>
        <div>
          <div className={s.tableHeader}>
            <span>Date and Time</span>
            <span>Operation Type</span>
            <span>Payment System</span>
            <span>Wallet</span>
            <span>Amount</span>
          </div>
          {his.map((el, i) => (
            <div className={s.elementContainer} key={i}>
              {el}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Operations;
