import React from "react";

import { Icon } from "../index";

const Header = ({ openLogin, curentPage, setPage, openSignUp, urlTo }) => {
  return (
    <div className="header-title">
      <div
        className="head-logo"
        onClick={() => curentPage!==undefined && setPage(curentPage - 1 < 0 ? 0 : curentPage - 1)}
      >
        <a href = {urlTo?urlTo:'7_option-ghp'}>
          <Icon name="logo" />
        </a>
      </div>
      <div className="buttons">
        <span onClick={() => openLogin()}>Login</span>
        <span onClick={() => openSignUp()} className="no-color">
          Sign Up
        </span>
      </div>
    </div>
  );
};

export default Header;
