import React from "react";

import "./fonts/fonts.css";
import "antd/dist/antd.css";
import { Provider } from "mobx-react";
import { Title, Agreement, Incides, MoneyPairs } from "./pages";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Store from "./store";

const stores = { Store };

const App = () => {
  
  return (
      <Provider {...stores}>
        <Router>
          <Switch>
            <Route path="/7_option-ghp/user-agreement" component={Agreement} />
            <Route path = "/7_option-ghp/money-pairs" component = {MoneyPairs} />
            <Route exact path = '/7_option-ghp/user' component = {Incides} />
            <Route exect path="/7_option-ghp" component={Title} />
          </Switch>
        </Router>
      </Provider>
  );
};

export default App;
