import React, { useState, useEffect } from "react";
import {observer} from 'mobx-react-lite'
// import _debounce from "lodash/debounce";

//components
import { LastPage, GraphicPage, Info, StartPage } from "../index";
import { Header, Icon, Login, Forgot, SignUp } from "../../components";
//styles
import "./index.css";

import store from "../../store";

const TitlePage = () => {
  const {
    setIsMobile,
    isForgotPasswordVisible,
    isLoginVisible,
    isSignUpVisible,
    setForgotPasswordVisibility,
    setLoginVisibility,
    setSignUpVisibility
  } = store;
  const [curentPage, setPage] = useState(0);

  const move = 48 - 4.5 * curentPage;

  const loginData = {
    visible: isLoginVisible,
    close: () => (setLoginVisibility(false)),
    forgotOpen: () => setForgotPasswordVisibility(true),
    openSignUp: () => setSignUpVisibility(true)
  };
  const forgotData = {
    visible: isForgotPasswordVisible,
    close: () => setForgotPasswordVisibility(false),
    openSignUp: () => setSignUpVisibility(true)
  };
  const signData = {
    visible: isSignUpVisible,
    close: () => setSignUpVisibility(false),
    openLogin: () => setLoginVisibility(true)
  };
  const headerData = {
    openLogin: () => setLoginVisibility(true),
    setPage,
    curentPage,
    openSignUp: () => setSignUpVisibility(true)
  };
  const startData = {
    openSignUp: () => setSignUpVisibility(true),
    next: () => setPage(1)
  };
  const chDt = {
    openSignUp: () => setSignUpVisibility(true)
  };

  const renderArr = [
    <StartPage {...startData} />,
    <GraphicPage {...chDt} />,
    <Info />,
    <LastPage />
  ];

  let arr = Array(4)
    .fill(<Icon name="pagination-dot" />)
    .map((val, i) => (
      <div
        className="pag-elem"
        key={i + 1}
        onClick={() => {
          setPage(i);
          window.scrollTo({
            top: i * window.innerHeight,
            behavior: "smooth"
          });
        }}
      >
        {val}
      </div>
    ));
  arr[curentPage] = (
    <div className="pagination-mouse" key={"mouse" + curentPage}>
      <Icon name="pagination-mouse" />
    </div>
  );

  // TODO:    useEffect
  useEffect(() => {
    // let cur = 0;
    // const handleScroll = e => {
    //   e.preventDefault()
    //   if (
    //     Store.isSignUpVisible ||
    //     Store.isForgotPasswordVisible ||
    //     Store.isLoginVisible
    //   )
    //     return;
    //   if (e.deltaY > 0) {
    //     window.scrollTo({
    //       top: (cur + 1) * window.innerHeight,
    //       behavior: "smooth"
    //     });
    //     cur += cur < 3 ? 1 : 0;
    //   } else {
    //     window.scrollTo({
    //       top: (cur - 1) * window.innerHeight,
    //       behavior: "smooth"
    //     });
    //     cur -= cur > 0 ? 1 : 0;
    //   }
    //   setPage(cur);
    // }
    const handleResize = () => {
      setIsMobile(window.innerWidth < 451);
    };
    window.addEventListener("resize", handleResize);
    // window.addEventListener("mousewheel", _debounce(handleScroll, 200));
    return () => {
      // window.removeEventListener("mousewheel", handleScroll);
      window.removeEventListener("resize", handleResize);
    };
  }, []);
  return (
    <div className="main-block-title">
      <Header {...headerData} />

      <Login {...loginData} />
      <Forgot {...forgotData} />
      <SignUp {...signData} />

      <div className="pages">
        {renderArr.map((elem, i) => (
          <div key={i} className="page">
            {elem}
          </div>
        ))}
      </div>
      <div className="pagination" style={{ left: move + "vw" }}>
        {arr.map(elem => elem)}
      </div>
    </div>
  );
};

export default observer(TitlePage);
