import React from "react";
export default {
  "Client agreement": (
    <div>
      <div className="title-name">Client agreement</div>
      <div className="descr">
        Dolphin Corp (a company incorporated under the laws of Saint Vincent and
        the Grenadines, registration number 25151 IBC 2018, with an office on
        the First Floor, First St. Vincent Bank Ltd Building, James Street,
        Kingstown, St.Vincent and the Grenadines, hereinafter  the Company),
        according to the terms and conditions of this Client Agreement
        (hereinafter  the Agreement), provides an individual (hereinafter  the
        Client) with access to the site https://binomo.com (hereinafter  the
        Website) and the Binomo mobile application (hereinafter  the Mobile
        Application) for the purpose of the Client concluding trading operations
        using financial instruments provided by the Company. <br />
        <br />
        Unless otherwise specifically stated in this Agreement, all services
        provided by the Company through the Website are also provided through
        the Mobile Application.
      </div>
      <div className="positions_1">1. General Provisions</div>
      <div className="descr">
        1.1. The Client accepts the Agreement by registering and creating an
        Account on the Website. Acceptance of the Agreement means full and
        unconditional agreement of the Client to its terms and conditions.
        <br />
        <br />
        1.2. The place of supply of services is Saint Vincent and the
        Grenadines.
        <br />
        <br />
        1.3. The Privacy Policy and the Cookie Policy published on the Website
        are integral parts of this Agreement.
      </div>
      <div className="positions_1">2. Terminology</div>
      <div className="descr">
        2.1. Account - the Client's account on the Website, necessary for
        authentication and the provision of access to their Personal Account.
        <br />
        <br />
        2.2. Asset - stock indices, stocks, commodities, and currency pairs
        available for trading using financial instruments provided by the
        Company. <br />
        <br />
        2.3. Account Balance - the total amount of funds in the Client's
        Account, excluding Open Deals. The amount of the Account Balance is the
        amount of the Company's financial obligation to the Client at a
        particular point in time, unless otherwise specified. <br />
        <br />
        2.4. Bonuses, Bonus Funds - funds credited by the Company to the
        Client's Account to increase their trading potential. Bonuses are not a
        financial obligation of the Company to the Client. <br />
        <br />
        2.5. Withdrawal of Funds - the debiting of monetary funds from the
        Account and transfer thereof to the Client's account. <br />
        <br />
        2.6. Deposit - monetary funds deposited by the Client in their Account.
        <br />
        <br />
        2.7. Closed Deal - a Deal in which the Expiration has been reached, or a
        Deal closed by order of the Client. <br />
        <br />
        2.8. Quote - the digital cost value of an Asset at a certain point in
        time. <br />
        <br />
        2.9. The Clients Personal Account - a section of the Website with
        restricted access through which the Client, having completed the
        authorization procedure by entering their login and password, is able to
        use the services of the Company. <br />
        <br />
        2.10. Log File - a file containing system information about the
        operation of the Company Server and information about the Clients
        actions on the Website. <br />
        <br />
        2.11. Non-trading Operation - an operation involving making a Deposit
        into the Account or Withdrawing Funds from the Client's Account, as well
        as other operations that are not considered Trading. <br />
        <br />
        2.12. Open Deal - a Deal prior to its Expiration or a Deal prior to its
        closing by order of the Client. <br />
        <br />
        2.13. Payment System Provider - a company that provides online services
        for conducting electronic payments. <br />
        <br />
        2.14. A Clients Real Account (Client Account, Account) - a special
        Client Account on the Trading Platform that reflects in real time the
        results of their Non-trading Operations, Open and Closed Deals, as well
        as other changes in the Company's financial obligations to the Client.
        Possible Account Currencies: US dollar, euro, Russian ruble. <br />
        <br />
        2.15. Deal - opposite Trading Operations with the same identification
        number. <br />
        <br />
        2.16. The Company Server - a software and hardware complex that provides
        streams of Quotes to the Client in real time and processes the Client's
        Trading Orders. <br />
        <br />
        2.17. Strike Price - the Asset Quote forecasted by the Client at the
        close of the Deal. <br />
        <br />
        2.18. Trading Activity - the Clients actions involving making a Deposit
        into their Account, Withdrawing Funds, concluding Trading Operations,
        paid registration for a tournament, crediting the balance of the
        Clients tournament account (re-buys), and activating Bonuses or gifts.
        <br />
        <br />
        2.19. Trading Operation - an over-the-counter operation with an Asset
        concluded between the Company and the Client in non-stop mode. <br />
        <br />
        2.20. Trading Platform - a computer system that is accessed through the
        Clients Personal Account and is used to display Quote streams
        transmitted from the Company Server, to place the Clients Trading
        Orders, to reflect the results of their execution, and to perform other
        actions related to the execution of Trading Operations. <br />
        <br />
        2.21. Trading Order - the Client's order to conclude a Trading Operation
        on the conditions specified in that order. <br />
        <br />
        2.22. Trading Mechanics - financial instrument derivatives of Assets
        provided by the Company on the Trading Platform, the usage of which does
        not result in the actual acquisition of the Asset. <br />
        <br />
        2.23. Trading Turnover - the sum of all Deals concluded by the Client
        since the last Deposit was made in their Account. <br />
        <br />
        2.24. Expiration - the moment of execution of the Deal.
      </div>
      <div className="positions_1">3. Trading Mechanics</div>
      <div className="descr">
        The following Trading Mechanics are available to the Client:
        <br /><br />
        3.1. "Standard" When opening a Deal, the Client specifies the Asset, the
        amount of the Deal, the direction of chart movement, and the time of the
        closing of the Deal. The Deal closes upon reaching the selected closing
        time. If at the moment of closing of the Deal, the current Quote of the
        selected Asset is higher than its Quote at the moment of the opening of
        the Deal, the Deal is considered profitable if the selected direction of
        chart movement is "up." If at the moment of closing of the Deal, the
        current Quote of the selected Asset is lower than its Quote at the
        moment of the opening of the Deal, the Deal is considered profitable if
        the selected direction of chart movement is "down." The yield on the
        Deal is fixed and depends on the selected amount of the Deal, the Asset,
        and the execution time of the Deal. The yield on the Deal is defined as
        the percentage yield multiplied by the amount of the Deal.
        <br /><br />
        3.2. "Strike"
        When opening a Deal, the Client specifies the Asset, the amount of the
        Deal, the direction of chart movement, the Strike Price, and the closing
        time of the Deal. The Deal closes upon reaching the selected closing
        time. If at the moment of the closing of the Deal, the current Quote of
        the selected Asset is higher than the Strike Price specified by the
        Client, the Deal is considered profitable if the selected direction of
        chart movement is "up." If at the moment of the closing the Deal, the
        current Quote of the selected Asset is lower than the Strike Price
        specified by the Client, the Deal is considered profitable if the
        selected direction of chart movement is "down." The yield on the Deal is
        fixed and depends on the selected amount of the Deal, the Asset, the
        Strike Price, the direction of chart movement, and the execution time of
        the Deal. The yield on the Deal is defined as the percentage yield
        multiplied by the amount of the Deal. The Deal can be closed early. The
        price of early execution is calculated dynamically and updated depending
        on the market conditions. "Strike" is available only on the Mobile
        application
      </div>
    </div>
  )
};
