import React, { useState } from "react";
import s from "./s.module.css";

const RadioBtn = ({ values, startCheckedValue, callback, countColumns }) => {
  const [currentValue, setCurrentValue] = useState(
    startCheckedValue || (values && values[0])
  );
  return (
    <div
      className={s.mainContainer}
      style = {{gridTemplateColumns: 'repeat('+countColumns+', 1fr)'}}
    >
      {values &&
        values.map((el, i) => (
          <div key={i} className={s.container}>
            <div className={s.radio} onClick = {() => setCurrentValue(el)}>
              <div className={currentValue === el ? s.selected : ""} />
            </div>
            <span className={s.value}>{el}</span>
          </div>
        ))}
    </div>
  );
};
export default RadioBtn