import React, { useState } from "react";
import s from "./s.module.css";

const DropDownPickerScroll = ({ values, callback, defaultVal, placeholder }) => {
  const [currentValue, setCurrentValue] = useState(defaultVal);
  const [isOpen, setIsOpen] = useState(false);
  return (
    <div className={s.mainStyleBlock}>
      <div className={s.foreightStyleBlock}>
        <div className={`${s.input} ${isOpen?s.greyColor:''}`}>
          <input placeholder={placeholder} defaultValue={currentValue} />
          <div className={`${s.arrow} ${isOpen?s.click:''}`} onClick = {()=>setIsOpen(!isOpen)}/>
        </div>
        <div
          className={`
          ${s.DDContent}
          ${isOpen ? s.open : ""}`}
        >
          {values.map((el, i) => (
            <span key={i} onClick = {() => {setCurrentValue(el); setIsOpen(false)}}>{el}</span>
          ))}
        </div>
      </div>
    </div>
  );
};
export default DropDownPickerScroll