import React, { useState } from "react";
import { Link } from "react-router-dom";

import { Icon } from "../index";
import { Drawer } from "antd";

import "./index.css";
import store from "../../store";

const Login = ({ visible, close, forgotOpen, openSignUp }) => {
  const { changeUser } = store;
  const [rem, setRem] = useState(false);
  const [email, setEmail] = useState("Example@gmail.com");
  const [isCorrect, setIsCorrect] = useState(false);
  const password = "root";

  return (
    <Drawer
      visible={visible}
      closable={false}
      placement="left"
      style={{ padding: 0, margin: 0 }}
      width={window.innerWidth > 450 ? 580 : "100vw"}
      destroyOnClose={true}
    >
      <div className="form-main-container">
        <div className="next-container-login">
          <div className="close" onClick={() => close()}>
            <Icon name="close-x" />
          </div>

          {window.innerWidth < 450 && (
            <div
              onClick={() => {
                openSignUp();
                close();
              }}
              className="sign-from-login"
            >
              Sign Up
            </div>
          )}

          <div className="position-logo" onClick={() => close()}>
            <Icon name="logo" />
          </div>
          <div className="grey big-text">
            <p>Login</p>
          </div>

          <div className="login-mail grey">
            <p>Your email</p>
            <input
              className="grey"
              placeholder="example@gmail.com"
              type="email"
              onChange={e => setEmail(e.target.value)}
            />
          </div>
          <div className="login-password grey">
            <p>Password</p>
            <input
              className="grey"
              placeholder="enter your password"
              type="password"
              onChange={e => {
                if (e.target.value === password) setIsCorrect(true);
                else setIsCorrect(false);
              }}
            />
          </div>
          <div className="check-box grey">
            <div onClick={() => setRem(!rem)}>
              <div
                style={{
                  background: rem ? "#FFF700" : "none",
                  width: "100%",
                  height: "100%",
                  borderRadius: "3px"
                }}
              />
            </div>
            <p>Remember me!</p>
          </div>
          {isCorrect ? (
            <Link to="/7_option-ghp/user">
              <span
                className="button-login no-color"
                onClick={() =>
                  changeUser(
                    email,
                    "https://www.stickpng.com/assets/images/585e4bf3cb11b227491c339a.png",
                    999999
                  )
                }
              >
                Enter
              </span>
            </Link>
          ) : (
            <span className="button-login no-color">Enter</span>
          )}
          <div
            className="re-text"
            onClick={() => {
              close();
              forgotOpen();
            }}
          >
            <p>Forgot password?</p>
          </div>
          <div
            className="re-text"
            onClick={() => {
              openSignUp();
              close();
            }}
          >
            <p>Create account </p>
          </div>
        </div>
      </div>
    </Drawer>
  );
};
export default Login;
