import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Icon } from "../index";
import { Drawer } from "antd";

import "./index.css";

const SignUp = ({ visible, close, openLogin }) => {
  const [sex, setSex] = useState(true);
  const [confirmPolity, setConfirmPolity] = useState(false);

  return (
    <Drawer
      visible={visible}
      closable={false}
      placement="left"
      style={{ padding: 0, margin: 0 }}
      width={window.innerWidth > 450 ? 580 : "100vw"}
      destroyOnClose={true}
    >
      <div className="form-main-container" style={{ height: "90vh" }}>
        <div className="container">
          <div className="close" onClick={() => close()}>
            <Icon name="close-x" />
          </div>

          {window.innerWidth < 450 && (
            <div
              onClick={() => {
                close();
                openLogin();
              }}
              className="login-from-sign yellow"
            >
              Login
            </div>
          )}

          <div className="position-logo" onClick={() => close()}>
            <Icon name="logo" />
          </div>
          <div
            className="grey format__text big-text"
            style={{ whiteSpace: "nowrap" }}
          >
            <p>Sign Up</p>
          </div>
          <div className="inputs__">
            <div className="login-mail grey input__format">
              <p>Name</p>
              <input className="grey" placeholder="create a name" />
            </div>
            <div className="login-mail grey input__forma">
              <p>Skype or Telegram</p>
              <input className="grey" placeholder="your account name" />
            </div>
            <div className="login-mail grey input__forma">
              <p>Your email</p>
              <input
                className="grey"
                placeholder="enter your email"
                type="email"
              />
            </div>
            <div className="login-mail grey input__forma">
              <p>Password</p>
              <input className="grey" placeholder="create a password" />
            </div>
          </div>

          <div className="currensy-picker grey">
            <div>
              <div className="container-point" onClick={() => setSex(true)}>
                <div className={sex ? "point" : ""} />
              </div>
              <span>Male</span>
            </div>
            <div>
              <div className="container-point" onClick={() => setSex(false)}>
                <div className={sex ? "" : "point"} />
              </div>
              <span>Female</span>
            </div>
          </div>
          <div className="check-box box">
            <div onClick={() => setConfirmPolity(!confirmPolity)}>
              <div
                style={{
                  background: confirmPolity ? "#FFF700" : "none",
                  width: "100%",
                  height: "100%",
                  borderRadius: "3px"
                }}
              />
            </div>
            <p className="text-confirm grey">
                     
              ,    
            </p>
          </div>
          <div className="sign-bottom-elements">
            <Link to="/7_option-ghp/user">
              <span className="button-login no-color">Start earning</span>
            </Link>
            <div
              onClick={() => {
                openLogin();
                close();
              }}
            >
              <p className="yellow">Already have account?</p>
              <p className="yellow"></p>
            </div>
          </div>
        </div>
      </div>
    </Drawer>
  );
};
export default SignUp;
