import React from "react";
import { Icon, Button } from "../../components";
import "./index.css";

const StartPage = ({ openSignUp, next }) => {
  return (
    <div className="title-data">
      <div className="left-text-box">
        <div className="title-text">
          <p>Start trading</p>
          <p className="yellow-text">OPTIONS</p>
          <p>NOW!</p>
        </div>
        <div className="small-text">
          <p>
            Earn on stock prices and currencies in 1 minute: guess where the
            chart will go, conclude a transaction - get up to 95% profit!
          </p>
        </div>
        <div className="under-text">
          <Button
            className="button no-color"
            onClick={() => openSignUp()}
            text="Sign Up"
          />
          <div
            className="yellow-text under-button-text"
            onClick={() => {
              next();
              window.scrollTo({ top: window.innerHeight, behavior: "smooth" });
            }}
          >
            <p className="yellow">how it works? </p>
          </div>
        </div>
      </div>
      <div className="phone-elem">
        <img src="https://svgshare.com/i/GmP.svg" alt="phone" />
      </div>
      <div className="shadow-phone">
        <Icon name="shadow" />
      </div>
      <div className="laptop-elem">
        <img src="https://svgshare.com/i/Gmw.svg" alt="laptop" />
      </div>
      <div className="laptop-shadow">
        <Icon name="shadow" />
      </div>
      <div className="circle-lins"></div>
    </div>
  );
};
export default StartPage;
