import React, { useState } from "react";
import { observer } from "mobx-react-lite";
import { Header, Login, Forgot, SignUp } from "../../components";

import "./index.css";
import dat from "./source";
import store from "../../store";

const UserAgreement = () => {
  const {
    isForgotPasswordVisible,
    isLoginVisible,
    isSignUpVisible,
    setLoginVisibility,
    setSignUpVisibility,
    setForgotPasswordVisibility
  } = store
  const [numberClicked, setNumberClicked] = useState(2);
  const [isOpenMenu, setIsOpenMenu] = useState(false);
  const menu = [
    "About company",
    "Privacy policy",
    "Client agreement",
    "Some link",
    "Other link"
  ];
  const currentDiaplayText = dat[menu[numberClicked]];

  const headerData = {
    openLogin: () => (setLoginVisibility(true)),
    openSignUp: () => (setSignUpVisibility(true)),
    urlTo: "/7_option-ghp/"
  };

  const loginData = {
    visible: isLoginVisible,
    close: () => (setLoginVisibility(false)),
    forgotOpen: () => (setForgotPasswordVisibility(true)),
    openSignUp: () => (setSignUpVisibility(true))
  };
  const forgotData = {
    visible: isForgotPasswordVisible,
    close: () => (setForgotPasswordVisibility(false)),
    openSignUp: () => (setSignUpVisibility(true))
  };
  const signData = {
    visible: isSignUpVisible,
    close: () => (setSignUpVisibility(false)),
    openLogin: () => (setLoginVisibility(true))
  };

  return (
    <div>
      <Login {...loginData} />
      <Forgot {...forgotData} />
      <SignUp {...signData} />
      <div
        className={"UA__header " + (isOpenMenu ? "block-back" : "unblock-back")}
      >
        <Header {...headerData} />
      </div>
      <div
        className={"menu-close " + (isOpenMenu && "open")}
        onClick={() => setIsOpenMenu(!isOpenMenu)}
      >
        <div className="manip">
          <div className="rect" />
          <div className="rect" />
        </div>
      </div>
      <div
        className={
          "nav " +
          (window.innerWidth < 640
            ? isOpenMenu
              ? "clicked_"
              : "before_click"
            : "desktop--nav")
        }
      >
        {menu.map((el, i) => (
          <div
            key={i}
            onClick={() => {
              setNumberClicked(i);
              setIsOpenMenu(false);
            }}
            style={{ color: i === numberClicked ? "#FFF700" : "" }}
          >
            {el}
          </div>
        ))}
      </div>

      <div className="main-text-description">{currentDiaplayText}</div>
    </div>
  );
};

export default observer(UserAgreement);
