import React, { useState, useRef, useEffect } from "react";
import { observer } from "mobx-react-lite";
import dataChart from "./chartSource";

import { Chart, Icon } from "../../components";

import "./index.css";
import store from "../../store";

const GraphicPage = ({ openSignUp }) => { 
  const {isMobile, setIsMobile} = store
  const res = 45;
  const green = 100 - res;
  const [canClick, setCanClick] = useState(true);

  const [clickedValue, setClickedValue] = useState("none");
  const [visibleArrows, setVisibilityArrows] = useState(true);

  const timerRef = useRef();
  //-------------------------------------------------------------------------------
  let refChart = useRef();
  const data = dataChart.dotsList();
  data.push({ x: "", y: null });
  data.push({ x: "", y: 0 });
  data.push({ x: "", y: null });
  data.push({ x: "18:03", y: 8 });

  const sliceDataPoints = (points, end) =>
    points.map((el, i) =>
      i < end
        ? el
        : i === data.length - 1 || i === data.length - 3
        ? el
        : { x: el.x, y: null }
    );
  const st = [
    {
      name: "value",
      width: 2,
      data: sliceDataPoints(data, 20)
    }
  ];
  const [series, setSeries] = useState(st);

  const chartData = {
    setIsMobile: val => setIsMobile(val),
    isMobile
  };
  const setNewData = () => {
    for (let i = 20; i < data.length; i++) {
      timerRef.current = setTimeout(
        () =>
          setSeries([
            {
              name: "value",
              width: 1,
              type: "area",
              data: sliceDataPoints(data, i)
            }
          ]),
        (i - 20) * 590
      );
    }
  };
  //-------------------------------------------------------------------------------
  const dataChart1 = {
    refChart,
    data,
    st,
    isMobile,
    setIsMobile,
    series,
    setSeries,
    chartData,
    setNewData,
    size: {
      width: "100%",
      height: isMobile ? 300 : "100%"
    }
  };

  useEffect(() => {
    setSeries([
      {
        name: "value",
        width: 1,
        type: "area",
        data: sliceDataPoints(data, 20)
      }
    ]);
    return () => {
      clearTimeout(timerRef.current);
      timerRef.current = undefined;
    };
  }, []);

  const back = `linear-gradient(90deg, #ff3a55 ${res}%, #00c873 ${green}%)`;

  const lineTrade = (
    <div className="line-trade">
      <div className="text-line">
        <span className="red">{res}%</span>
        <span className="grey text-percent">most opinion</span>
        <span className="green">{green}%</span>
      </div>
      <div className="color-data" style={{ background: back }} />
    </div>
  );
  const buttonUp = (
    <div
      className="button up"
      onClick={() => {
        if (canClick) {
          setCanClick(false);
          setVisibilityArrows(false);
          setNewData();
          setClickedValue("wait");
          setTimeout(() => setClickedValue("up"), 5500);
        }
      }}
    >
      <Icon name="arrow-up" />
      <p className="colorLeft">Up</p>
    </div>
  );
  const buttonDown = (
    <div
      className="button down"
      onClick={() => {
        if (canClick) {
          setCanClick(false);
          setVisibilityArrows(false);
          setNewData();
          setClickedValue("wait");
          setTimeout(() => setClickedValue("down"), 5500);
        }
      }}
    >
      <Icon name="arrow-down" />
      <p className="colorLeft">Down</p>
    </div>
  );

  const objRightSide = {
    none: isMobile ? (
      <div>
        {lineTrade}
        <div className="buttons-mobile">
          {buttonDown}
          {buttonUp}
        </div>
      </div>
    ) : (
      <div>
        {buttonUp} {lineTrade} {buttonDown}
      </div>
    ),
    up: (
      <div className="main__container">
        <div className="second__container">
          <div>
            <span className="yellow money__">188,00&euro;</span>
            <br />
            <span className="grey income__ ml10">your income</span>
          </div>
          <p className="grey small-description__">
            Congratulations your presict was correct!
          </p>
          <div className="button no-color btn__" onClick={() => openSignUp()}>
            Start Earning
          </div>
        </div>
      </div>
    ),
    wait: (
      <div className="grey wait__">
        <span>Wait for result...</span>
      </div>
    ),
    down: (
      <div className="main__container">
        <div className="second__container">
          <div>
            <span className="grey money__">0,00&euro;</span>
            <br />
            <span className="grey income__">your income</span>
          </div>
          <p className="grey small-description__">
            Unfortunately your presict was wrong!
          </p>
          <div
            className="button no-color btn__"
            onClick={() => {
              setCanClick(true);
              setVisibilityArrows(true);
              setSeries(st);
              setClickedValue("none");
            }}
          >
            Try again
          </div>
        </div>
      </div>
    )
  };

  return (
    <div className="g--page">
      {visibleArrows && <div className="arrows_circle" />}
      {visibleArrows && (
        <div className="arrows__">
          <div className="big-arrows">
            <Icon name="big-arrows" />
          </div>
          <Icon name="arows-with-lines" />
        </div>
      )}
      <div className="left-side">
        <span className="main-text-taste grey">
          Taste the
          <span className="yellow">victory</span>
        </span>

        <div className="chart-block">
          <Chart {...dataChart1} />
        </div>
      </div>
      <div className="text-arrows">
        {clickedValue === "none" && (
          <div>
            <p className="yellow">It's really simple!</p>
            <p className="grey">Where will the chart move next? Up or Down?</p>
          </div>
        )}
        {objRightSide[clickedValue]}
      </div>
    </div>
  );
};

export default observer(GraphicPage);
