import React, { useState } from "react";
import { HeaderMenuItem, Button } from "../../../components";
import s from "./s.module.css";
import { DatePicker } from "antd";
import history from "./data";

import store from "../../../store";

const Deals = ({ gotoDashboard }) => {
  const { user } = store;
  const [dateForFilter, setDateForFilter] = useState();
  const [currentOperationsFilter, setOperationsFilter] = useState("All");
  const [isFilterOpen, setIsFilterOpen] = useState(false);
  const accountsTypes = ["All", "Demo", "Live"];

  const createView = array =>
    array.map(el => (
      <>
        <div>
          <span>{el.active.pair}</span> <br />
          <span>{el.active.percents}%</span>
        </div>
        <div>
          <span>{`
          ${el.date.start.getDate()}.${
            el.date.start.getMonth() + 1 < 10
              ? "0" + (el.date.start.getMonth() + 1)
              : el.date.start.getMonth() + 1
          }.${el.date.start.getYear()} ${el.date.start.getHours()}:${el.date.start.getMinutes()}:${el.date.start.getSeconds()}
          `}</span>
          <br />
          <span>{`
          ${el.date.end.getDate()}.${
            el.date.end.getMonth() + 1 < 10
              ? "0" + (el.date.end.getMonth() + 1)
              : el.date.end.getMonth() + 1
          }.${el.date.end.getYear()} ${el.date.end.getHours()}:${el.date.end.getMinutes()}:${el.date.end.getSeconds()}
          `}</span>
        </div>
        <span>{el.opening}</span>
        <span>{el.closing}</span>
        <span>{el.tradeID}</span>
        <span>{el.trade}</span>
        <span>{el.income}</span>
        <span>{el.status}</span>
      </>
    ));

  const [data, setData] = useState(createView(history));
  return (
    <div className={s.main}>
      <HeaderMenuItem text="Deals" gotoDashboard={gotoDashboard} />
      <div className={s.mainPage}>
        <div className={s.balance}>
          <span>Balance</span>
          <div>
            <img src={user.image} alt="" />
            <div className={s.userName}>
              <span>Total</span>
              <span>${user.currencyCount.live}</span>
            </div>
          </div>
        </div>
        <div className={s.horizontalLine} />
        <div className={s.pickers}>
          <div className={s.picker}>
            <div>
              <span>Date</span>
              <DatePicker
                placeholder="From"
                className={s.datePicker}
                onChange={val =>
                  setDateForFilter({ start: val._d, end: dateForFilter.end })
                }
              />
              <div className={s.arrow} />
            </div>
          </div>
          <div className={s.picker}>
            <div>
              <span>Date</span>
              <DatePicker
                placeholder="To"
                className={s.datePicker}
                onChange={val =>
                  setDateForFilter({ start: dateForFilter.start, end: val })
                }
              />
              <div className={s.arrow} />
            </div>
          </div>
          <div className={s.picker}>
            <div>
              <span>Account</span>
              <div>{currentOperationsFilter}</div>
              <div
                className={`${s.arrow} ${isFilterOpen ? s.active : ""}`}
                onClick={() => setIsFilterOpen(!isFilterOpen)}
              />
              <div
                className={`${s.ddContent} ${isFilterOpen ? s.open : s.close}`}
              >
                {accountsTypes.map((el, i) => (
                  <div
                    key={i}
                    onClick={() => {
                      setOperationsFilter(el);
                      setIsFilterOpen(false);
                    }}
                  >
                    {el}
                  </div>
                ))}
              </div>
            </div>
          </div>
          <Button className={s.btnConfirm} text="Confirm" />
        </div>
        <div>
          <div className={s.tableHeader}>
            <span>Active</span>
            <span>Date and Time</span>
            <span>Opening</span>
            <span>Closing</span>
            <span>Trade ID</span>
            <span>Trade $</span>
            <span>Income $</span>
            <span>Status</span>
          </div>

          {data.map((el, i) => (
            <div key={i} className={s.tableItem}>
              {el}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Deals;
