import React, { useState } from "react";
import s from "./index.module.css";

const MoneyPair = ({img, moneyPair, percents, key, click, unClick}) => {
  const [isClicked, setClicked] = useState(false)
  let variant = () => {!isClicked?click(moneyPair):unClick(moneyPair); setClicked(!isClicked)}
  return (
    <div className = {s.pair} key = {key} onClick = {variant} >
      <img src={img} alt="" />
      <span className = {isClicked?'yellow':''}>{moneyPair}</span>
      <span>{percents}%</span>
    </div>
  );
};
export default MoneyPair