import React, { useState } from "react";
import s from "./s.module.css";
import store from "../../../store";
import TimezonePicker from "react-timezone";
import {
  HeaderMenuItem,
  Input,
  DropDownPickerScroll,
  RadioButton,
  Button,
  Icon
} from "../../../components";
import {Link} from 'react-router-dom'

const UserInf = ({gotoDashboard}) => {
  const { user } = store;
  const [timezone, setTimezone] = useState();
  const mail = user.email.split("@");
  return (
    <div className={s.mainBlock}>
      <HeaderMenuItem text="Profile" gotoDashboard = {gotoDashboard}/>
      <div className={s.mainPage}>
        <span>Personal data</span>
        <div className={s.userI}>
          <img src={user.image} alt="user img" />
          <div>
            <div>
              <span className="yellow">{mail[0]}</span>
              <span>@{mail[1]}</span>
            </div>
            <span>ID: {user.id}</span>
          </div>
        </div>
        <div className={s.inputsHolder}>
          <div className={s.defInput}>
            <Input defaultValue={user.name} title="Name" callback={val => {}} />
          </div>
          <div className={s.defInput}>
            <Input
              defaultValue={user.surname}
              title="Surname"
              callback={val => {}}
            />
          </div>
          <div className={s.defInput}>
            <span className={s.blockName}>Birthday</span>
            <div>
              <DropDownPickerScroll
                defaultVal={user.birthday.day}
                placeholder="Day"
                values={Array(31)
                  .fill()
                  .map((el, i) => (i < 9 ? "0" : "") + (i + 1))}
              />
            </div>
            <div>
              <DropDownPickerScroll
                defaultVal={user.birthday.month}
                placeholder="Month"
                values={[
                  "January",
                  "February",
                  "Math",
                  "April",
                  "May",
                  "June",
                  "July",
                  "August",
                  "September",
                  "October",
                  "November",
                  "December"
                ]}
              />
            </div>
            <div>
              <DropDownPickerScroll
                defaultVal={user.birthday.year}
                placeholder="Year"
                values={Array(2000 - 1970 + 1)
                  .fill()
                  .map((el, i) => i + 1970)
                  .reverse()}
              />
            </div>
          </div>
          <div className={s.defInput}>
            <TimezonePicker
              value={timezone}
              onChange={timezone => setTimezone(timezone)}
              inputProps={{
                placeholder: "Select Timezone...",
                name: "timezone"
              }}
            />
          </div>
        </div>
        <div className={s.radioChecker}>
          <RadioButton values={["Male", "Female"]} countColumns={2} />
        </div>
        <Button text="Apply changes" className={s.buttonConfirmChanges} />
        <div className={s.horizontalLine} />
        <div className={s.mainChangePassword}>
          <span>Change password</span>
          <div>
            <Input title="Old password" placeholder="enter old password" inputType = 'password'/>
            <div />
            <Input title = 'New password' placeholder = 'create password' inputType = 'password'/>
            <Input title = 'New password' placeholder = 'repeat password' inputType = 'password'/>
          </div>
          <Button text = 'Save new password' className = {s.btnSaveNewPassword}/>
        </div>
        <div className={s.horizontalLine} />
        <div className = {s.wallets}> 
        <span>My wallets</span>
        <span>You don`t have any wallets yet</span>
        </div>
        <div className={s.horizontalLine} />
        <div className = {s.bottomData}>
              <span className = {s.grey}><Link to="/7_option-ghp/user-agreement">About company</Link></span>
              <span className = {s.grey}><Link to="/7_option-ghp/user-agreement">Privacy policy</Link></span>
              <span className = {s.grey}><Link to="/7_option-ghp/user-agreement">Customer agreement</Link></span>
              <div>
                  <Icon name = 'social-instagram'/>
                  <Icon name = 'social-facebook' />
                  <Icon name = 'social-telegram' />
              </div>
        </div>
      </div>
    </div>
  );
};

export default UserInf;
