import React, { useState } from "react";
import styles from "./index.module.css";
import { UserInf, Icon } from "../index";
import { Link } from "react-router-dom";

const UserMenu = ({ user, callback }) => {
  const [isMenuClicked, setIsMenuClicked] = useState(false);
  const mainMenuItems = ["Dashboard", "Deposit", "Withdraw", "Operations", "Deals"]
  const foreightMenuItems = ["About cmpany", "Privacy policy", "Customer agreement"]
  return (
    <div
      className={`
          ${styles.menu}
          ${isMenuClicked ? styles.open : styles.close}
          `}
      onClick={() => !isMenuClicked && setIsMenuClicked(true)}
    >
      {/* double lines when drawer close & x when drawer open */}
      <img
        src={
          isMenuClicked
            ? "https://svgshare.com/i/H32.svg" //x
            : "https://svgshare.com/i/H2h.svg" //lines
        }
        alt = ''
        onClick={() => setIsMenuClicked(!isMenuClicked)}
      />
      <div className={styles.menuItems}>
        <div className={styles.userBlock} onClick = {() => {callback(0); setIsMenuClicked(false)}}>
          <UserInf {...user} />
        </div>
        <div className={styles.itemsNav}>
          {mainMenuItems.map(
            (elem, index) => (
              <span key={index} onClick = {() => {callback(index+1); setIsMenuClicked(false)}}>{elem}</span>
            )
          )}
        </div>
        <div className = {styles.companyInformation}>
          {foreightMenuItems.map(
            (el, ind) => (
              <span key={ind}>{el}</span>
            )
          )}
        </div>
        <span className = {styles.logout}><Link to = '/7_option-ghp'>Logout </Link></span>
        <div className = {styles.socialIcons}>
            <div><Icon name = "social-instagram"/></div>
            <div><Icon name = "social-facebook"/></div>
            <div><Icon name = "social-telegram"/></div>
        </div>
      </div>
    </div>
  );
};
export default UserMenu;
