import React from "react";
import { HeaderMenuItem, Icon } from "../../../components";
import store from "../../../store";
import s from "./s.module.css";
import {Link} from 'react-router-dom'

const Deposit = ({gotoDashboard}) => {
  const { user } = store;
  return (
    <div className = {s.main}>
      <HeaderMenuItem text="Deposit" gotoDashboard = {gotoDashboard}/>
      <div className={s.mainPage}>
        <div className={s.user}>
          <span>Balance</span>
          <div>
            <img src={user.image} alt="" />
            <div>
              <span>Total</span>
              <span>${user.currencyCount.live}</span>
            </div>
          </div>
        </div>
        <div className={s.horizontalLine} />
        <div className={s.paymentMethods}>
          <span>Payment method</span>
          <div>
            <div>
              <Icon name="sponsor-paytm" />
            </div>
            <div>
              <Icon name="sponsor-skrill" />
            </div>
            <div>
              <Icon name="sponsor-neteller" />
            </div>
          </div>
          <div>
            <div>
              <Icon name="sponsor-perfectMoney" />
            </div>
            <div>
              <Icon name="sponsor-perfectVoucher" />
            </div>
          </div>
        </div>
        <div className={s.horizontalLine} />
        <div className={s.horizontalLine} />
        <div className = {s.bottomData}>
              <span className = {s.grey}><Link to="/7_option-ghp/user-agreement">About company</Link></span>
              <span className = {s.grey}><Link to="/7_option-ghp/user-agreement">Privacy policy</Link></span>
              <span className = {s.grey}><Link to="/7_option-ghp/user-agreement">Customer agreement</Link></span>
              <div>
                  <Icon name = 'social-instagram'/>
                  <Icon name = 'social-facebook' />
                  <Icon name = 'social-telegram' />
              </div>
        </div>
      </div>
    </div>
  );
};

export default Deposit;
