import React, { useState } from "react";
import { HeaderMenuItem, Input, Button, Icon } from "../../../components";
import s from "./s.module.css";
import store from "../../../store";
import { Link } from "react-router-dom";

const Withdraw = ({ gotoDashboard }) => {
  const { user } = store;
  const [isOpen, setIsOpen] = useState(false)
  const [curentPaymentMethod, setPaymentMethod] = useState("paytm");
  const mail = user.email.split("@");
  return (
    <div className={s.container}>
      <HeaderMenuItem text="Withdraw" gotoDashboard={gotoDashboard} />
      <div className={s.main}>
        <div className={s.accountBalance}>
          <span>Account balance</span>
          <div className={s.account}>
            <img src={user.image} alt="" />
            <div>
              <div className={s.mail}>
                <span className="yellow">{mail[0]}</span>
                <span>@{mail[1]}</span>
              </div>
              <span>ID: {user.id}</span>
              <div className={s.availableFunds}>
                <span>Available funds</span>
                <br />
                <span className="yellow">${user.currencyCount.live}</span>
              </div>
              <div className={s.total}>
                <span>Total: </span>
                <span>${user.currencyCount.live}</span>
              </div>
            </div>
          </div>
        </div>
        <div className={s.horizontalLine} />
        <div className={s.request}>
          <div>
            <span>Request for a withdrawal</span>
            <div>
              <div className={s.paymentMethods}>
                <span>Payment method</span>
                <div className={s.ddPaymentContainer}>
                  <div>
                    <div>{curentPaymentMethod}</div>
                    <div onClick = {() => setIsOpen(!isOpen)} className = {isOpen?s.active:s.noActive}/>
                  </div>
                </div>
                <div className={s.ddPaymentValues} style = {{display: isOpen?'flex':'none'}}>
                    <div>
                        <div className = {s.sponsor} onClick = {() => {setPaymentMethod('paytm'); setIsOpen(false)}}>
                            <Icon name = 'sponsor-paytm' />
                        </div>
                        <div className = {s.sponsor} onClick = {() => {setPaymentMethod('skrill'); setIsOpen(false)}}>
                            <Icon name = 'sponsor-skrill' />
                        </div>
                        <div className = {s.sponsor} onClick = {() => {setPaymentMethod('neteller'); setIsOpen(false)}}>
                            <Icon name = 'sponsor-neteller' />
                        </div>
                    </div>
                    <div>
                        <div className = {s.sponsor} onClick = {() => {setPaymentMethod('perfect money'); setIsOpen(false)}}>
                            <Icon name = 'sponsor-perfectMoney' />
                        </div>
                        <div className = {s.sponsor} onClick = {() => {setPaymentMethod('perfect voucher'); setIsOpen(false)}}>
                            <Icon name = 'sponsor-perfectVoucher' />
                        </div>
                    </div>
                </div>
              </div>
            </div>
            <div className={s.defInput}>
              <div className={s.tempI}>
                <Input title="Specify amount" defaultValue="$0" />
              </div>
              <span>Withdrawal from $7.00</span>
            </div>
            <div className={s.defInput}>
              <Input
                title="Wallet number"
                placeholder="enter your wallet number"
              />
            </div>
          </div>
          <div>
            <span>Description</span>
            <div>
              <span>The minimal sum to withdraw $7;</span>
              <br />
              <br />
              <span>
                The money will be debided from the account at the moment of
                request`s payment
              </span>
              <br />
              <br />
              <span>
                The withdrawal is made by the same payment method that you have
                used to deposit money into the system
              </span>
            </div>
          </div>
        </div>
        <div>
          <Button className={s.buttonRequest} text="Send a request" onClick = {() => {console.log('send request clicked')}}/>
        </div>
        <div className={s.horizontalLine} />
        <div className={s.withdrawalRequests}>
          <span>Withdrawal requests</span>
          <div>
            <span>You don`t have any request yet</span>
          </div>
        </div>
        <div className={s.horizontalLine} />
        <div className={s.bottomData}>
          <span className={s.grey}>
            <Link to="/7_option-ghp/user-agreement">About company</Link>
          </span>
          <span className={s.grey}>
            <Link to="/7_option-ghp/user-agreement">Privacy policy</Link>
          </span>
          <span className={s.grey}>
            <Link to="/7_option-ghp/user-agreement">Customer agreement</Link>
          </span>
          <div>
            <Icon name="social-instagram" />
            <Icon name="social-facebook" />
            <Icon name="social-telegram" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Withdraw;
