import React from "react";

import { Icon } from "../index";
import { Drawer } from "antd";

import "./index.css";

const Forgot = ({ visible, close, openSignUp }) => {
  return (
    <Drawer
      visible={visible}
      closable={false}
      placement="left"
      style={{ padding: 0, margin: 0 }}
      width={window.innerWidth > 450 ? 580 : "100vw"}
      destroyOnClose={true}
    >
      {window.innerWidth < 450 && (
        <div
          onClick={() => {
            openSignUp();
            close();
          }}
          className="sign-from-login"
        >
          Sign Up
        </div>
      )}

      <div className="form-main-container">
        <div className="main-pass-container">
          <div className="close" onClick={() => close()}>
            <Icon name="close-x" />
          </div>
          <div className="position-logo" onClick={() => close()}>
            <Icon name="logo" />
          </div>
          <div className="yellow back-to" onClick={() => close()}>
            <p> Back to Login</p>
          </div>
          <div className="grey big-text">
            <p style={{ lineHeight: 1.2 }}>Forgot password</p>
          </div>
          <div className="login-mail grey">
            <p>Your email</p>
            <input
              className="grey"
              placeholder="enter your email"
              type="email"
            />
          </div>
          <span className="button-login button-send no-color">
            Send me new password
          </span>
          <div
            className="yellow back-to"
            style={{ marginTop: "4vh" }}
            onClick={() => {
              openSignUp();
              close();
            }}
          >
            <p>Create account  </p>
          </div>
        </div>
      </div>
    </Drawer>
  );
};

export default Forgot;
