import React from "react";
import styles from "./style.module.css";
import { Icon, DropDownSelect, Button, ValueSelect } from "../../../components";
import store from "../../../store";
import { Link } from "react-router-dom";

const Dashboard = () => {
  const {user} = store;

  const getRandom = (from, to) => Math.random() * (to - from) + from;

  const res = 45;
  const green = 100 - res;

  const back =
    "linear-gradient(90deg, #ff3a55 " + res + "%, #00c873 " + green + "%)";

  const lineTrade = (
    <div className="line-trade">
      <div className="text-line">
        <span className="red">{res}%</span>
        <span className="grey text-percent">most opinion</span>
        <span className="green">{green}%</span>
      </div>
      <div className="color-data" style={{ background: back }} />
    </div>
  );
  const buttonUp = (
    <div className="button up" style={{ marginBottom: 30 }}>
      <Icon name="arrow-up" />
      <p>Up</p>
    </div>
  );
  const buttonDown = (
    <div className="button down" style={{ marginTop: 30 }}>
      <Icon name="arrow-down" />
      <p>Down</p>
    </div>
  );

  const nowOnline = 25778;
  const addedCurrency = [];
  const currentTypeOfCurrency = "EUR/USD";
  const minutes = Array(30)
    .fill()
    .map((el, id) => id + 1);
  const prices = Array(7)
    .fill()
    .map((el, i) => (
      <div className={styles.priceCntainer}>
        <span className={styles.history}>
          <span
            className={i + getRandom(0, 20) > 10 ? styles.red : styles.green}
          >
            {String(getRandom(100, 2000)).split(".")[0]}.00$
          </span>
          <br />
          <span>{currentTypeOfCurrency}82%</span>
        </span>
      </div>
    ));
  prices[0] = (
    <div className={styles.priceCntainer}>
      <span className={styles.history + " " + styles.firstBlock}>History</span>
    </div>
  );

  const settingsAmount = {
    source: Array(100)
      .fill()
      .map((el, id) => 100 + id * 5),
    titleText: "Amount",
    preText: "$",
    iconPlus: (
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "100%",
          width: "100%"
        }}
      >
        <div
          style={{
            width: 30,
            height: 6,
            background: "#828ea0",
            borderRadius: 4
          }}
        />
        <div
          style={{
            width: 6,
            height: 30,
            background: "#828ea0",
            borderRadius: 4,
            marginLeft: -18
          }}
        />
      </div>
    ),
    iconMinus: (
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "100%",
          width: "100%"
        }}
      >
        <div
          style={{
            width: 30,
            height: 6,
            background: "#828ea0",
            borderRadius: 4
          }}
        />
      </div>
    )
  };
  const settingsMinutesSelect = {
    source: minutes,
    titleText: "Interval",
    postText: "min",
    iconPlus: (
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "100%",
          width: "100%"
        }}
      >
        <div
          style={{
            width: 30,
            height: 6,
            background: "#828ea0",
            borderRadius: 4
          }}
        />
        <div
          style={{
            width: 6,
            height: 30,
            background: "#828ea0",
            borderRadius: 4,
            marginLeft: -18
          }}
        />
      </div>
    ),
    iconMinus: (
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "100%",
          width: "100%"
        }}
      >
        <div
          style={{
            width: 30,
            height: 6,
            background: "#828ea0",
            borderRadius: 4
          }}
        />
      </div>
    )
  };
  return (
    <div className={styles.mainContainer}>
      <div className={styles.main}>
        <div className={styles.logotype}>
          <Icon name="logo" />
        </div>
        <div className={styles.user}>
          <Icon name="user" />
          <span>{nowOnline} online</span>
        </div>
        <div className={styles.chartType}>
          <span>Type of graphic: </span>
        </div>
        <div className={styles.interval}>
          <DropDownSelect values={["1 min", "2 min", "5 min", "10 min", "30 min"]} />
        </div>
        <div className={styles.accountType}>
          <DropDownSelect
            values={[
              <div className={styles.typesOfAccs}>
                <span>Live account</span>
                <span>${user.currencyCount.live}</span>
              </div>,
              <div className={styles.typesOfAccs}>
                <span>Demo account</span>
                <span>${user.currencyCount.demo}</span>
              </div>
            ]}
          />
        </div>
        <div>
          <Button
            className={styles.button}
            onClick={() => alert("click open deposit")}
            text="Deposit+"
          />
        </div>
      </div>
      <div className={styles.currencyContainer}>
        <Link to="/7_option-ghp/money-pairs">
          <div className={styles.currencyPlus}>
            <Icon name="plus" />
          </div>{" "}
        </Link>
        {addedCurrency.map((el, i) => (
          <div key={i}>{el}</div>
        ))}
      </div>

      <div className={styles.mainBigBlock}>
        <div className={styles.mainChartWithPrice}>
          {/*---------------chart-----------------*/}
          <div className={styles.chartBlock}>HERE WILL BE CHART</div>
          <div className={styles.price}>
            {prices.map((el, id) => (
              <div key={id}>{el}</div>
            ))}
          </div>
        </div>
        {/* right side  */}
        <div>
          <div className={styles.selectVal} style={{ marginTop: 10 }}>
            <ValueSelect {...settingsMinutesSelect} />
          </div>
          <div className={styles.selectVal} style={{ marginTop: 50 }}>
            <ValueSelect {...settingsAmount} />
          </div>

          <div className={styles.posibleIncome}>
            <span style={{ width: "5vw" }}>Posible income</span>
            <div>
              <span style={{ color: "#00c873", fontSize: "24pt" }}>82%</span>
              <br />
              <span style={{ color: "yellow" }}>$410.82</span>
            </div>
          </div>
          <div className={styles.buttons}>
            {buttonUp}
            {lineTrade}
            {buttonDown}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
