import React, { useState } from "react";
import { observer } from "mobx-react-lite";
import { MoneyPair } from "../../components";
import { Link } from "react-router-dom";

import s from "./index.module.css";
import store from "../../store";

const MoneyPairs = observer(() => {
  const {
    addSelectedPair,
    removeSelectedPair,
    selectedPairs,
    moneyPairs
  } = store;
  const [selectedFilter, setSelectedFilter] = useState(0);
  const typesFilter = ["All", "Currency", "ETS", "Crypto", "CMD"];
  return (
    <div className={s.page}>
      <div className={s.mainPageContainer}>
        {/* x */}
        <div>
          <Link to="/7_option-ghp/user">
            <img src = 'https://svgshare.com/i/H32.svg' alt = '' />
          </Link>
        </div>
        {/* selectedPairs & search */}
        <div>
          <div className={s.selectedPairs}>
            {selectedPairs.map((el, i) => (
              <div key={i}>{el}</div>
            ))}
          </div>
          <div>
            <div className={s.searchContainer}>
              <div className={s.secondSearchContainer}>
                <img src="https://svgshare.com/i/H9z.svg" alt="" />
                <input className={s.search} placeholder="Search" />
              </div>
            </div>
          </div>
        </div>
        {/* left menu */}
        <div className={s.menuItems}>
          {typesFilter.map((el, i) => (
            <span
              key={i}
              className={i === selectedFilter ? s.active : ""}
              onClick={() => setSelectedFilter(i)}
            >
              {el}
            </span>
          ))}
        </div>
        {/* moneyPairs  */}
        <div className={s.pairsCntainer}>
          {moneyPairs.map((el, i) => (
            <MoneyPair
              key={i}
              {...el}
              click={currentPair => addSelectedPair(currentPair)}
              unClick={currentPair => removeSelectedPair(currentPair)}
            />
          ))}
        </div>
      </div>
    </div>
  );
});
export default MoneyPairs;
