import React from "react";
import { Link } from "react-router-dom";

import "./index.css";
import { Icon } from "../../components";

const LastPage = () => {
  return (
    <div className="main-page-block">
      <div className="main-text5 grey">
        <span>Withdraw </span>
        <span>any amount</span>
      </div>
      <br />
      <span className="main-text5 yellow">at any moment!</span>

      <div className="sponsors">
        <div className="sponsor-paytm">
          <Icon name="sponsor-paytm" />
        </div>
        <div className="sponsor-skrill">
          <Icon name="sponsor-skrill" />
        </div>
        <div className="sponsor-neteller">
          <Icon name="sponsor-neteller" />
        </div>
        <div className="sponsor-perfectMoney">
          <Icon name="sponsor-perfectMoney" />
        </div>
        <div className="sponsor-perfectVoucher">
          <Icon name="sponsor-perfectVoucher" />
        </div>
      </div>

      <div className="warning grey">
        <span className="general">General risk warning</span>
        <p>
          The financial operations offered on this site may involve increased
          risk. By using the financial services and tools this site offers, you
          may suffer serious financial loss, or completely lose the funds in
          your guaranteed-trading account. Please evaluate all the financial
          risks and seek advice from an independent financial advisor before
          trading. 7option is not responsible for any direct, indirect or
          consequential losses, or any other damages resulting from the users
          actions on this site.
        </p>
      </div>

      <div className="bottom-list grey">
        <div className="left">
          <Link to="/7_option-ghp/user-agreement">
            <p
              className="grey"
              onClick={() => setTimeout(() => window.scrollTo(0, 0), 0)}
            >
              About company
            </p>
          </Link>
          <Link to="/7_option-ghp/user-agreement">
            <p
              className="grey"
              onClick={() => setTimeout(() => window.scrollTo(0, 0), 0)}
            >
              Privacy policy
            </p>
          </Link>
          <Link to="/7_option-ghp/user-agreement">
            <p
              className="grey"
              onClick={() => setTimeout(() => window.scrollTo(0, 0), 0)}
            >
              Customer agreement
            </p>
          </Link>
        </div>
        <div className="right">
          <Icon name="social-instagram" />
          <Icon name="social-facebook" />
          <Icon name="social-telegram" />
        </div>
      </div>
    </div>
  );
};
export default LastPage;
